<template>
  <div id="app">
    <qna-header></qna-header>
    <router-view></router-view>
  </div>
</template>

<script>

import Vue from 'vue';
import VueRouter from 'vue-router';

import QnaHeader from './components/QnaHeader.vue';
import QnaList from './components/QnaList.vue';
import QnaDetail from './components/QnaDetail.vue';
import QnaInput from './components/QnaInput.vue';



Vue.use(VueRouter);


const router = new VueRouter({
  mode : 'history',
  routes : [
    {path:'/',component:QnaList},
    {path:'/list',component:QnaList},
    {path:'/input',component:QnaInput},
    {path:'/detail/:no',component:QnaDetail}
  ]
});

export default {
  name: 'app',
  components: {
    QnaHeader
  },
  router,
  data() {
    return {
      todoItems: [],
      todo : {}
    }
  }, 
  created() {
    this.$router.push("/");
  },
}
</script>

