<template>
  <div>
    <ul>
      <li v-for="(qnaItem) in qnalist" v-bind:key="qnaItem.qnaNo" class="shadow" 
      @click="getQna(qnaItem.qnaNo)">

        {{qnaItem.qnaTitle}} : {{qnaItem.qnaContent}} : {{qnaItem.qnaUserid}} : {{qnaItem.qnaDatetime}}
        <span
          class="removeBtn"
          type="button"
          @click.stop="removeQna(qnaItem.qnaNo)"
        >
          <i class="far fa-trash-alt"></i>
        </span>
      </li>
    </ul>
  </div>
</template>


<script>
import Constant from '../Constant';

export default {
  computed: {
    qnalist() {
      return this.$store.state.qnaItems; 
    }
  },
  created () {
    this.$store.dispatch(Constant.GET_QNALIST);
  },
   methods: {
    getQna(no) {
      console.log('getTodo : '+no);
      this.$store.dispatch(Constant.GET_QNA,{no});
      this.$router.push('/detail/'+no);
    },
    removeQna(no){
      console.log('removeTodo : '+no);
      this.$store.dispatch(Constant.REMOVE_QNA,{no});
    }
  }, 

  // methods: {
  //   getTodo(no) {
  //     console.log('getTodo :::: '+no);
  //     this.$router.push('/detail/'+no);
  //   },
  //   ...mapActions([
  //     Constant.REMOVE_TODO,Constant.COMPLETE_TODO
  //   ])
  // },

};
</script>

<style scoped>
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}
li {
  display: flex;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 5px;
}
.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 5px;
}
.removeBtn {
  margin-left: auto;
  color: #de4343;
}

.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-move {
  transition: transform 1s;
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
.done{
  background-color: lightslategray;
}
</style>