import Vue from 'vue';
import Vuex from 'vuex';
import Constant from './Constant.js';
import http from './http-common.js';
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        qnaItems: [],
        qna: {},

    },
    actions: { // 외부와의 통신을 통해 데이터 처리 dispath에 의해 호출됨.

        /*         'getTodoList': function(store) {
                    http.get('/todolist/user/' + 'java')
                        .then(response => store.commit(Constant.GET_TODOLIST, { todoItems: response.data }))
                        .catch(exp => alert('getTodoList처리에 실패하였습니다.' + exp));
                }, */
        [Constant.GET_QNALIST]: store => {
            http.get('/qna')
                .then(response => store.commit(Constant.GET_QNALIST, { qnaItems: response.data }))
                .catch(exp => alert('getTodoList처리에 실패하였습니다.' + exp));
        },
        [Constant.GET_QNA]: (store, payload) => {
            http.get('/qna/' + payload.no)
                .then(response => store.commit(Constant.GET_QNA, { qna: response.data }))
                .catch(exp => alert('getTodo처리에 실패하였습니다.' + exp));
        },
        [Constant.ADD_QNA]: (store, payload) => {
            http.post('/qna', {
                    qnaTitle: payload.qna.qnaTitle,
                    qnaContent: payload.qna.qnaContent,
                    qnaUserid: 'ssafy',
                    qnaDatetime : new Date().getTime()
                })
                .then(() => {
                    console.log('추가하였습니다.');
                    store.dispatch(Constant.GET_QNALIST);
                })
                .catch(exp => alert('추가 처리에 실패하였습니다.' + exp));
        },
        [Constant.MODIFY_QNA]: (store, payload) => {
            http.put('/qna/' + payload.qna.qnaNo, {
                    qnaNo: payload.qna.qnaNo,
                    qnaTitle: payload.qna.qnaTitle,
                    qnaContent: payload.qna.qnaContent,
                    qnaUserid: payload.qna.qnaUserid,
                    qnaDatetime: payload.qna.qnaDatetime,
                    replyContent: payload.qna.replyContent,
                    replyDatetime: payload.qna.replyDatetime,
                    replyUserid: payload.qna.replyUserid
                })
                .then(() => {
                    console.log('수정하였습니다.');
                    store.dispatch(Constant.GET_QNALIST);
                })
                .catch(exp => alert('수정 처리에 실패하였습니다.' + exp));
        },
        [Constant.REMOVE_QNA]: (store, payload) => {
            http.delete('/qna/' + payload.no)
                .then(() => {
                    console.log('삭제하였습니다.');
                    store.dispatch(Constant.GET_QNALIST);
                })
                .catch(exp => alert('삭제 처리에 실패하였습니다.' + exp));

        },
    },
    mutations: { // 저장소에 데이터 실제 반영(commit시 호출됨)
        [Constant.GET_QNALIST]: (state, payload) => {
            state.qnaItems = payload.qnaItems;
        },
        [Constant.GET_QNA]: (state, payload) => {
            console.log(payload);
            state.qna = payload.qna;
        },


    }
});


export default store;