<template>
  <div>
    <table>
      <tbody>
        <tr>
          <td>번호</td>
          <td>
            <input type="text" v-model="qna.qnaNo" readonly />
          </td>
        </tr>
        <tr>
          <td>제목</td>
          <td>
            <input type="text" v-model="qna.qnaTitle" />
          </td>
        </tr>
        <tr>
          <td>작성일</td>
          <td>
            <input type="text" step="1" v-model="qna.qnaDatetime" readonly />
          </td>
        </tr>
        <tr>
          <td>내용</td>
          <td>
            <input type="text" v-model="qna.qnaContent" />
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <span class="addContainer" @click="modifyQna">
              <div class="modifyBtn fas fa-pen-square" aria-hidden="true"></div>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  created() {
    this.getQna(this.$route.params.no);
  },
  computed: {
    qna: {
      get() {
        // 화살표함수 사용하면 안됨. this : undefined 로 나옴.
        console.log("QnaInput computed get");
        // console.log(this.$store.state.qna);
        return this.$store.state.qna;
      },
      set(newQna) {
        console.log("QnaInput computed set...");
        this.qna.qnaNo = newQna.qnaNo;
        this.qna.qnaTitle = newQna.qnaTitle;
        this.qna.qnaContent = newQna.qnaContent;
        this.qna.qnaUserid = newQna.qnaUserid;
        this.qna.qnaDatetime = newQna.qnaDatetime;
        this.qna.replyContent = newQna.replyContent;
        this.qna.replyDatetime = newQna.replyDatetime;
        this.qna.replyUserid = newQna.replyUserid;
      }
    }
  },
  methods: {
    getQna(no) {
      console.log("getTodo : " + no);
      this.$store.dispatch(Constant.GET_QNA, { no });
    },
    modifyQna() {
      if (this.qna.qnaContent.trim() != "" && this.qna.qnaTitle.trim() != "") {
        console.log("할일 수정:: ");
        console.log(this.qna);
        this.$store.dispatch(Constant.MODIFY_QNA, { qna: this.qna });
        this.$router.push("/");
      } else {
        console.log("공백입력.");
      }
      this.clear();
    },
    clear() {
      this.todo = {}; 
    },
  }
}
</script>

<style scoped>
